
.login .logo{
	display: block;
    width: 2.3rem;
    margin: .8rem auto;
}
.login{
	width:90%;
	margin:0 auto;
}
.login-btn{
	background: #f46094;
	color:#fff;
	border-radius:1rem;
	font-size: .5rem;
}
.login-btn:active{
	background:#f45555;
}
.login .forget-register{
	display:-ms-flexbox;
	display:flex;
	color:#adb3b9;
	font-size:.4rem;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	padding:.3rem .5rem;
}
.login h1{
	color:#81bbf1;
	font-size:.5rem;
	text-align: center;
}
.login .other-way,.login .title{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
}
.login .title{
	-ms-flex-pack: justify;
	    justify-content: space-between;
	font-size: .4rem;
	color:#adb3b9;
	margin-bottom: .2rem;
}
.login .title div{
	width:25%;
	overflow: hidden;
}
.login .title h2{
	padding-top:.1rem;
}
.login .other-loagin{
	margin:.6rem 0 .3rem;
}
.login .other-way img{
	height:.7rem;
}
.login .protocol{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
}
.login .protocol .yd-checkbox{
   	padding:0;
}
.login .protocol .yd-checkbox-text{
	color:#adb3b9;
	font-size:.3rem;
}
.login .protocol a{
	color:#81bbf1;
	font-size: .3rem;
	padding-top: .04rem;
}



